: 0;\n}\n\n*, *::before, *::after { \n\tbox-sizing: border-box; \n}\n\nhtml { \n\tscroll-behavior: smooth; \n}\n \nbody {\n  background-color: #393E46;\n  color: #EEEEEE;\n  font-family: 'inter', sans-serif;\n  min-height: 100vh; \n}\n\n/*\n  Skip to content\n*/\n\n.skip-to-content {\n  background-color: #393E46;\n  color: #00ADB5;\n  position: fixed;\n  top: -200px;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 16px;\n  border-radius: 10px;\n  text-decoration: none;\n  z-index: 100;\n}\n\n.skip-to-content:focus {\n  top: 0;\n  left: 0;\n  margin: 75px 10px;\n}\n\n/*\n  Loader content\n*/\n\n.loader:before,\n.loader:after,\n.loader {\n  border-radius: 50%;\n  width: 2.5em;\n  height: 2.5em;\n  -webkit-animation-fill-mode: both;\n  animation-fill-mode: both;\n  -webkit-animation: load7 1.8s infinite ease-in-out;\n  animation: load7 1.8s infinite ease-in-out;\n}\n\n.loader {\n  font-size: 10px;\n  margin: 80px auto;\n  position: relative;\n  text-indent: -9999em;\n  -webkit-transform: translateZ(0);\n  -ms-transform: translateZ(0);\n  transform: translateZ(0);\n  -webkit-animation-delay: -0.16s;\n  animation-delay: -0.16s;\n}\n\n.loader:before {\n  left: -3.5em;\n  -webkit-animation-delay: -0.32s;\n  animation-delay: -0.32s;\n}\n\n.loader:after {\n  left: 3.5em;\n}\n\n.loader:before,\n.loader:after {\n  content: '';\n  position: absolute;\n  top: 0;\n}\n\n@-webkit-keyframes load7 {\n  0%,\n  80%,\n  100% {\n    box-shadow: 0 2.5em 0 -1.3em #ffffff;\n  }\n  40% {\n    box-shadow: 0 2.5em 0 0 #ffffff;\n  }\n}\n\n@keyframes load7 {\n  0%,\n  80%,\n  100% {\n    box-shadow: 0 2.5em 0 -1.3em #ffffff;\n  }\n  40% {\n    box-shadow: 0 2.5em 0 0 #ffffff;\n  }\n}\n\n/* \n  Add animation to content \n*/\n\n.animate-bottom {\n  position: relative;\n  -webkit-animation-name: animatebottom;\n  -webkit-animation-duration: 1s;\n  animation-name: animatebottom;\n  animation-duration: 1s\n}\n\n@-webkit-keyframes animatebottom {\n  from { bottom:-100px; opacity:0 } \n  to { bottom:0px; opacity:1 }\n}\n\n@keyframes animatebottom { \n  from{ bottom:-100px; opacity:0 } \n  to{ bottom:0; opacity:1 }\n}\n \n/*\n  AppBar\n*/\n\n.app-bar {\n  display: grid;\n  grid-template-columns: auto 1fr auto;\n  gap: 10px;\n  position: sticky; \n  background-color: #222831;\n  top: 0;\n  padding: 10px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\n  z-index: 99;\n}\n \n.app-bar .app-bar__menu {\n  display: flex;\n  align-items: center;\n  \n}\n \n.app-bar .app-bar__menu button {\n  background-color: transparent;\n  color: #EEEEEE;\n  border: none;\n  font-size: 16px;\n  padding: 10px; \n  min-width: 44px;\n  min-height: 44px;\n  cursor: pointer;\n}\n \n.app-bar .app-bar__brand {\n  display: flex;\n  flex-direction: row-reverse;\n  align-items: center;\n}\n \n.app-bar .app-bar__brand h1 {\n  color: #00ADB5;\n  font-size: 24px;\n  text-transform: uppercase;\n  user-select: none;\n}\n \n.app-bar .app-bar__navigation {\n  position: absolute;\n  background-color: #222831;\n  top: 60px;\n  left: -180px;\n  width: fit-content;\n  padding: 10px;\n  transition: all 0.3s;\n  overflow: hidden;\n}\n \n.app-bar .app-bar__navigation.open {\n  left: 0;\n}\n\n.app-bar .app-bar__navigation ul li a {\n  display: flex;\n  gap: 10px;\n  color: #EEEEEE;\n  font-size: 16px;\n  width: fit-content;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 10px;\n  margin: 10px; \n  align-items: center;\n  text-decoration: none;\n}\n\n.app-bar .app-bar__navigation ul li a:hover,\n.app-bar .app-bar__navigation ul li a:active {\n  transition: ease-in-out;\n  transition-duration: 0.2s; \n  background-color: #393E46;\n  color: #00ADB5;\n  border-radius: 10px;\n}\n\n/* \n  Hero \n*/\n\n.hero {\n  display: grid;\n  align-content: center;\n  height: 350px;\n  width: 100%;\n  color: white;\n  text-align: center;\n  background: linear-gradient(180deg, #12151a 0%, #252a3477 50%, #12151a 100%);\n  background-position: center;\n  padding: 24px;\n  position: relative;\n}\n  \n.hero picture,\n.hero img {\n  width: 100%;\n  height: 100%;\n}\n\npicture {\n  position: absolute;\n  z-index: -1;\n}\n\nimg {\n  object-fit: cover;\n}\n\n.hero__heading {\n  font-size: 24px;\n}\n\n.hero__tagline {\n  margin-top: 10px;\n  font-family: monospace;\n  font-size: 16px;\n  font-weight: bold;\n}\n\n/*\n  Main Content\n*/ \n\nmain {\n  padding: 20px;\n  flex: 1;\n}\n \n.content {\n  margin: 0 auto;\n  min-height: 100%;\n}\n \n.content .content__heading {\n  font-size: 24px;\n  font-weight: 600;\n  text-align: center;\n}\n \n/*\n  Restaurants \n*/\n\n.restaurants {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 20px;\n  margin: 32px 0;\n}\n \n/*\n  Restaurant Item\n*/\n\n.restaurants-item {\n  width: 100%;\n  background-color: #EEEEEE;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\n  border-radius: 10px;\n  overflow: hidden;\n}\n \n.restaurants-item__header {\n  position: relative;\n}\n \n.restaurants-item .restaurants-item__header .restaurants-item__header__poster {\n  width: 100%;\n  height: 200px;\n}\n \n.restaurants-item .restaurants-item__header .restaurants-item__header__rating {\n  position: absolute;\n  padding: 10px;\n  top: 20px;\n  right: 0;\n  border-top-left-radius: 25px; \n  border-bottom-left-radius: 25px;\n  display: inline-block;\n  background-color: #222831;\n  color: #EEEEEE;\n  font-size: 14px;\n}\n\n.restaurants-item .restaurants-item__header .restaurants-item__header__rating i {\n  color: #ffff00;\n}\n \n.restaurants-item .restaurants-item__header .restaurants-item__header__rating .restaurants-item__header__rating__score {\n  margin-left: 10px;\n}\n\n.restaurants-item .restaurants-item__header .restaurants-item__header__city {\n  position: absolute;\n  padding: 10px;\n  left: 0;\n  top: 20px;\n  border-top-right-radius: 25px;\n  border-bottom-right-radius: 25px;\n  display: inline-block;\n  background-color: #222831;\n  color: #EEEEEE;\n  font-size: 14px;\n}\n\n.restaurants-item